
import Scroller from 'vux/src/components/scroller/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { comList,wxAutoLogin } from "@/js/service/getData";
import func from '@/js/handleFunction';
export default {
name:'Home',
data () {
return {
pageIndex:1,
pageSize:10,
lists: [],
titleRight:'历史回顾',
pullupConfig2: {
content: '',
downContent: '',
upContent: '',
loadingContent: '加载中...'




},
show1:true,
classid:'32',
params:{
cmd:'achieve',
params:{
activity_type:'fdc74db2-9c79-292f-564f-83c97d9ba788', //活动主题
province:'',
city:'',
type: 1,  //1进行中或未开始  2已结束
page:1,
pageSize:8
}
},  //活动列表params
}
},
directives:{
TransferDom
},
components: {
Scroller,Divider,Spinner,XHeader,Drawer,ViewBox,Loading
},
created(){
this.initData();
},
methods: {
initData(){
comList(this.params).then(res => {
this.show1 = false;
this.lists = res.data.data.detail;
})
},


loadList () {
this.params.params.page++;
let _this = this;
_this.$nextTick(() => { _this.$refs.cPull.reset();});

comList(this.params).then(res => {
if(res.data.data.detail.length){
for (let i = 0; i < res.data.data.detail.length; i++) {
_this.lists.push(res.data.data.detail[i]);
}

setTimeout(() => {

_this.$refs.cPull.donePullup();
}, 100);
}
})
},
onItemClick (act_id,activity_type) {
this.$router.push({path:'/battleDetail',query:{act_id:act_id,activity_type:activity_type}});
},
}

}



