
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {applyForm, applyPay, submitForm} from "../../../js/service/getData";
import func from "../../../js/handleFunction";
import wx from 'weixin-js-sdk';
export default {
components: {
Group,
GroupTitle,
Cell,
XInput,
Selector,
PopupPicker,
XAddress,
Datetime,
XNumber,
XTextarea,
XSwitch,
XButton
},
data () {
return {
addressData: ChinaAddressData,
addressValue: ['广东省', '深圳市', '南山区'],
time1: '2017-06-01',

list:[],
numberValue: 0,
sendInfo:{
data:'',
file:{},
content:'',
},
info:{},
infoIndex:{},
content:'',
videoFile:{},
logintoken:func.getLocalStorage('logintoken'),
loginuid:func.getLocalStorage('loginuid'),
params:{
sign_type:2, //0视频报名 1图片报名 2音乐报名
act_id:this.$route.query.act_id,//活动id
act_type:this.$route.query.activity_type,//活动类型
national:'',
province:'',
city:'',
country:'',
address:'',
langtitude:'',
latitude:'',
videoID:'',
photo:'',
music:'',
source:'pc',
info:{},//动态表单信息
friends:'',
permission:1,//1公开 2隐私 3部分好友
content:'',//报名内容  用于音乐广场显示
}
}
},
created() {
this.initData();
},
mounted(){

},
methods: {
async initData() {
let p = {"logintoken":this.logintoken,"loginuid":this.loginuid,"params":{"act_id":this.$route.query.act_id}};
let res = await applyForm(p);

res = res.data.data;

let arr = [];

for(let i = 0; i<res.length;i++){
if(res[i].type == 'select'){
for(let j = 0;j<res[i].extra_option.length;j++){
arr.push(res[i].extra_option[j].name);
}
}
}
this.list.push(arr);
this.info = res;
},

submitForm(){
this.params.info = this.infoIndex;
let params = {
loginuid:this.loginuid,
logintoken:this.logintoken,
params:this.params,
};
submitForm(params).then(res => {
if(res.data.err_msg == 'success'){

if(res.data.data.pay == 1){
this.applyPay(res.data.data);
}else{
alert('报名成功');
this.$router.go(-1);
}
}else{
alert(res.data.info)
}
})
},

applyPay(data){
let openid = localStorage.getItem('openid');
let p = {
loginuid:this.loginuid,
logintoken:this.logintoken,
params:{
pay_type:2,
pay_platform:1,
goodsSystem_id:data.goodsSystem_id,
order_no:data.order_no,
activity_id:data.activity_id,
openid:openid,
return_url:window.location.href,
}
};
applyPay(p).then(res => {
if(res.data.err_msg == 'success'){
let data = res.data.data;
wx.config({
debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
appId: data.appid, // 必填，公众号的唯一标识
timestamp: data.timestamp, // 必填，生成签名的时间戳
nonceStr: data.noncestr, // 必填，生成签名的随机串
signature: data.sign,// 必填，签名
jsApiList: [
'chooseWXPay'
] // 必填，需要使用的JS接口列表
});
wx.ready(function(){
wx.chooseWXPay({
timestamp: data.timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
nonceStr: data.noncestr, // 支付签名随机串，不长于 32 位
package: data.packageValue, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
signType:data.signtype, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
paySign:data.sign, // 支付签名
success: function (res) {
window.history.go(-1);

},
fail: function(res){
alert('支付失败');
}
});
})
}else{
alert('支付失败')
}

});
}
}
}



