import staticData from '../js/config/url-config'
import {setLike} from "./service/getData";
import {getDocumentPort, getViewPort} from "./func";

//通用方法
var func = {
    /**
     * 数据格式转换 广场图片贴
     * @param arr
     * @returns {*}
     */
    converData:function(arr){
        let data = arr;
        for(let i = 0;i < data.length;i++){
            let newarr = '';
            let str;
            str = data[i].cover;

            if(str.indexOf(',') != -1){ //等于-1代表没查到
                newarr = str.split(',');
                if(newarr.length > 4){
                    data[i].cover = newarr.slice(0,4);
                }else{
                    data[i].cover = newarr;
                }
            }else{
                let arr = [];
                arr[0] = data[i].cover;
                data[i].cover = arr;
            }
        }
        return data;
    },

    /**
     * 微信地址重定向 获取code
     * @param str
     */
    wxRedirect:function(str){
        var ua = navigator.userAgent.toLowerCase();
        var w = window.innerWidth;
        if(ua.match(/MicroMessenger/i)=="micromessenger") {
            let appid ='wx0e7332e9adc4fffc';
            let ECurl = encodeURIComponent(str);
            // let url = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid='+appid+'&redirect_uri='+ECurl+'&response_type=code&scope=snsapi_userinfo#wechat_redirect';
            // let url = 'https://h5.yinyueriji.net/get-weixin-code.html?appid='+appid+'&redirect_uri='+ECurl+'&response_type=code&scope=snsapi_userinfo#wechat_redirect';

            let url = staticData.openWx + appid+'&redirect_uri='+ECurl+'&response_type=code&scope=snsapi_userinfo&connect_redirect=1#wechat_redirect';
            window.location.replace(url);
        }else if( w<=414&&( /iphone/g.test(ua) || /android/g.test(ua))){

        }
    },
    /**
     * 倒计时
     * @param st 开始时间
     * @param ed 结束时间
     * @returns {string}
     */
    countTime(st,ed,type){
        var start = st.replace(new RegExp(/-/gm) ,"/");
        var end = ed.replace(new RegExp(/-/gm) ,"/");

        var currentTime = new Date();
        var startTime = new Date(start);
        var endTime = new Date(end);

        var currentTime1 = currentTime.getTime();
        var startTime1 = startTime.getTime();
        var endTime1 = endTime.getTime();


        if(currentTime1 - startTime1 < 0){
            var params = {
                isEnd:0 //未开始
            }
        }else{
            //结束时间-当前时间 = 倒计时
            var d,h,m,s,isEnd,liftTime;
            if(type == 0){
                liftTime = endTime1 - currentTime1;//剩余时间
            }else{
                liftTime = endTime1 - startTime1;
            }

            if (liftTime >= 0) {
                d = Math.floor(liftTime/1000/60/60/24);//天
                h = Math.floor(liftTime/1000/60/60%24);//时
                m = Math.floor(liftTime/1000/60%60);//分
                s = Math.floor(liftTime/1000%60);//秒
            }else{
                isEnd = 1;//已结束
                d = 0;
            }

            var params = {
                isEnd:isEnd,
                d:d,
                h:h,
                m:m
            }
        }
        return params;
    },

    /**
     * 上拉滚动加载
     * @param scrollTimer  定时器
     * @param callback  回调
     * @returns {number | *}
     */
    upglideScroll(scrollTimer,callback){
        let view = getViewPort();
        let docPort = getDocumentPort();
        let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        let timer;

        if(scrollTimer) {
            clearTimeout(scrollTimer);
        }

        // console.log(scrollTop + view.height+'///'+docPort.height)
        timer = setTimeout(() => {
            if (scrollTop + view.height >= docPort.height) {
                callback();
            }
        })

        return timer;
    },
    /**
     * 本地储存 的用户信息
     * @param str
     * @returns {null}
     */
    getLocalStorage(str){
        let tokens = localStorage.getItem('tokens');
        if(!tokens){
            return null;
        }else{
            let data = JSON.parse(tokens);
            switch (str){
                case 'logintoken':
                    return data.logintoken;
                    break;
                case 'loginuid':
                    return data.loginuid;
                    break;
                default:
                    break;
            }
        }
    },
    /**
     * 获取地址栏 或者本地存储
     * @param that
     * @param str
     * @returns {*}
     */
    getWindowLocalStorage(that,str) {
        let params = that.$route.query;

        if(params[str] != undefined){
            return params[str];
        }else{
            let tokens = localStorage.getItem('tokens');
            if(!tokens){
                return null;
            }else{
                let data = JSON.parse(tokens);
                switch (str){
                    case 'logintoken':
                        return data.logintoken;
                        break;
                    case 'loginuid':
                        return data.loginuid;
                        break;
                    default:
                        break;
                }
            }
        }
    },
    /**
     * 格式化参数
     * @param p
     * @returns {{loginuid: *|null, logintoken: *|null, params: *}}
     */
    trimParams(p){
        return {
            'loginuid':func.getLocalStorage('loginuid'),
            'logintoken':func.getLocalStorage('logintoken'),
            params:p,
        }
    },
    /**
     * 从地址栏获取登录信息
     * @param p
     * @returns {{loginuid: *|null, logintoken: *|null, params: *}}
     */
    trimParamsWindow(that,p){
        return {
            'loginuid':func.getWindowLocalStorage(that,'loginuid'),
            'logintoken':func.getWindowLocalStorage(that,'logintoken'),
            params:p,
        }
    },
    handleLike(dynamic_type,detail_id){
        let params = {
            type:dynamic_type,
            detail_id:detail_id,
            source:'h5',
        };

        let p = new Promise((resolve,reject)=>{
            setLike(func.trimParams(params)).then(res => {
                resolve(res);
            })
        });

        return p;
    }

}

export default func;



