
import foot from '../../../components/footer/comItem-foot.vue';
import staticData from '../../../js/config/url-config.js';
import { comInfo, comItem } from "../../../js/service/getData";
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Scroller from 'vux/src/components/scroller/index.vue'

import func from '../../../js/handleFunction.js'
import {wxshare} from "../../../js/wx/share";
export default {
name: 'index',
mounted(){
window.addEventListener('scroll',this.handleScroll);
},
directives:{
TransferDom
},
data(){
return{
user:'',
resData:[],
id:'',
userid:'',
pageIndex:1,
pageSize:10,
lists: [],
pullupConfig2: {
content: '上拉加载更多',
downContent: '松开进行加载',
upContent: '上拉加载更多',
loadingContent: '加载中...'
},
defaultImg:"https://greattone4.oss-cn-shanghai.aliyuncs.com/h5/default/2018062210150549.jpg",
classid:'',
loadImg:true,
params:{
cmd:'achieve',
params:{
act_id:this.$route.query.act_id, //活动id
}
},  //活动详情params
dynamicParams:{
loginuid:'',
logintoken:'',
params:{
act_id:this.$route.query.act_id, //活动id
page:1,
pageSize:12,
}
},  //活动报名列表
isTabActive:true, //活动介绍tab
scrollTimer:'',
}
},
created(){

this.initList();
},
components:{
XHeader,Search,Drawer,ViewBox,Scroller,foot
},
methods:{
handleScroll(){
if(!this.isTabActive){
this.scrollTimer = func.upglideScroll(this.scrollTimer,this.loadList);
}
},

async initList(){
let res,res1;

res = await comInfo(this.params);
document.title = res.data.data.name;
this.resData = res.data.data;
this.loadImg = false;

res1 = await comItem(this.dynamicParams);
this.lists = func.converData(res1.data.data.list);

let url = window.location.href;
let p = {
current_url:url
};
let m = {
link:url,
title:this.resData.name,
desc:'来自 @' + this.resData.user_name + ' 的音乐日记',
imgUrl:this.resData.cover_pic,
};

wxshare(p,m).then(function (res) {

});
},

async loadList () {
let res,lists;

this.dynamicParams.params.page++;
res = await comItem(this.dynamicParams);
if(res.data.err_msg == 'success'){
lists = func.converData(res.data.data.list);
lists.forEach((item,index) => {
this.lists.push(item);
})
}
},
changeTab($event){
let classname = $event.currentTarget.className;
if(classname == ''){
this.isTabActive = !this.isTabActive;
}
},
onItemClick (detail_id) {
this.$router.push({path:'/CompetitionContent',query:{detail_id:detail_id,act_id:this.resData.act_id,dynamic_type:1}});
location.reload();
},
openList(){
this.$refs.child.popupMain();
},
},
}



