
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {applyForm} from "../../../js/service/getData";
import staticData from "../../../js/config/url-config";

export default {
components: {
Group,
GroupTitle,
Cell,
XInput,
Selector,
PopupPicker,
XAddress,
Datetime,
XNumber,
XTextarea,
XSwitch,
XButton
},
data () {
return {
isShowLoading:true,
list:{},
havaCallFlag:false,
sendInfo:{
data:'',
content:'',
},
info:{},
infoIndex:{},
fileImg:this.getPic('icon/form_add.png'),
act_id:'',
}
},
created() {
this.act_id = this.$route.query.act_id;
this.initData();
},
mounted(){





},
methods: {
async initData() {
let p = {"logintoken":"","loginuid":"","params":{"act_id":this.act_id}};
let res = await applyForm(p);
res = res.data.data;
this.pushSelectData(res);

this.info = res;
this.isShowLoading = false;
},

pushSelectData(res){
for(let i = 0; i<res.length;i++){
if(res[i].type == 'select'){
let arr = [];
let arr1 = [];
for(let j = 0;j<res[i].extra_option.length;j++){
arr.push(res[i].extra_option[j].name);
}
this.list[res[i].name] = arr1;
arr1.push(arr);
}
}
},
triggerInput(){
document.querySelector('.selectImg').click();
},
getPic(str){
return staticData.picurl+str;
},

getBase64Img(){
let _this = this;
this.$bridge.callhandler('selectVideoFile','', function(data) {
_this.havaCallFlag = true;
_this.fileImg = data;

})
},

submitForm(){
let dom = document.getElementsByTagName('label');

for(let i = 0;i < dom.length; i++){
if(this.infoIndex[dom[i].innerText] == undefined || this.infoIndex[dom[i].innerText] == ''){
this.$vux.toast.text(`${dom[i].innerText}不能为空`,'middle');
return false;
}
}

if(this.$refs.video){
if(!this.havaCallFlag){
this.$vux.toast.text('请选择视频','middle');
return false;
}
}

for(let i = 0; i<this.info.length;i++){
if(this.info[i].type == 'textarea'){
this.sendInfo.content = this.infoIndex[this.info[i].mark];
}
}

this.sendInfo.data = this.infoIndex;

console.log(this.sendInfo);
this.$bridge.callhandler('submitFromWeb',this.sendInfo, function(responseData) {

})
}
}
}



