
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Toast from 'vux/src/components/toast/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import {isLogin, setAttention, setLike} from "../../../js/service/getData"
import CommentLayer from './comment'
import GiftLayer from './gift'
import DownLoad from '../../../components/header/download'
import func from '../../../js/handleFunction'
import AdvertLayer from '../../../components/advert/advert'
import VoteLayer from './vote'
import GiveReward from './giveReward'
import Vue from 'vue'
import {wxshare} from "../../../js/wx/share.js";
import {createScript, getUserLocalStorage, isAndroidSystem} from "../../../js/func";
import Cnzz from '../../../components/cnzz/index'
import staticData from '../../../js/config/url-config'
export default {
name: "wx-video",
data(){
return{
show:false,
show1:false,
show2:false,
show3:false,
toastValue:false,
isGiftLayer:false,
isDownLoadShow:true,    //头部下载app显示
shade:-1,
toastText:'',
loading:true,

isAdvertShow:true,  //广告显示
defName:'未选择',
defPrice:null,
defGuan:'+ 关注',
position: 'default',
pageIndex:1,
pageSize:20,
lists:[],
saytext:'',
logintoken:getUserLocalStorage(this,'logintoken'),
loginuid:getUserLocalStorage(this,'loginuid'),
fullScreen:false,//wx是否全屏 默认false
playIcon:true,
activity_id:'',     //活动id
giftList:[],        //礼物列表
balance:0,      //爱乐币余额
giftid:null,        //礼物id
zanNum:0,


commentList:[],     //子组件——评论列表
isCommentShow:false,
classid:'',
countDown:'',       //倒计时
countProgress:20,      //倒计时进度条
videoScreen:false,

isZan:false,
}
},
directives:{
TransferDom
},
components:{
CommentLayer,GiftLayer,DownLoad,AdvertLayer,VoteLayer,GiveReward,Cnzz,
Popup,Toast,Grid,GridItem,GroupTitle,Group,Cell,Flexbox,FlexboxItem,XInput,XTextarea,PopupHeader,XButton,
XHeader,XDialog
},
props:{
resData:{
type:Object,
required:true,
},
userinfo:{
type:Object,
required:true,
},
resDetail:{
type:Object,
required:true,
},
ownerId:{
type:String,
required:true,
}
},
updated(){
let player = document.getElementById('tcVideo');
player.controls = false;
this.initVideo(player);
},
computed: {
isActivity() {
if(this.$route.query.dynamic_type == 4){
return true;
}
},
isNoPic(){
if(this.resData.hai_photo != 'http://www.greattone.net/e/data/images/nouserpic.gif'){
return true;
}else{
return false;
}
},

zanFlag(){
this.isZan = this.resDetail.is_like == 1?true:false;
return this.resDetail.is_like == 1?true:false;
},

isGuanzhu(){
return this.resDetail.is_fans == 1?false:true;
}
},
watch:{
show1(val,oldVal){
if(val == true){
document.body.style['width'] = '100%';
document.body.style['overflow'] = 'hidden';
document.body.style['position'] = 'fixed';
}else{
document.body.style['overflow'] = 'auto';
document.body.style['position'] = 'relative';
}
},
},
mounted(){
createScript('https://823/load.min.js')
this.hhhh();
},
created(){
this.classid = this.$route.query.classid;
let url = window.location.href;
let p = {
current_url:url
};
let m = {
link:url,
title:this.userinfo.username + ' 的音乐日记',
desc:this.resData.content,
imgUrl:this.userinfo.head_pic,
};
wxshare(p,m).then(function (res) {

});
},
methods:{
upRefrensh(){
this.$emit('refrensh');
},
refrenshCommentNum(data){
Vue.set(this.resData,'plnum',data);
},
handleScroll(e){

let scrollTop = this.$refs.viewBox.scrollTop;
let viewHeight = this.$refs.viewBox.clientHeight;
let scrollHeight = this.$refs.viewBox.scrollHeight;

if(scrollTop + viewHeight >= scrollHeight){

}

},
initVideo(player){
let _this = this;
if(isAndroidSystem() == true){
player.addEventListener('x5videoenterfullscreen',function(){
player.style.width = window.screen.width +'px';
player.style.height = window.screen.height +'px';
document.body.classList.add('fullscreen');
_this.fullScreen = true;
_this.isDownLoadShow = false;
});

player.addEventListener('x5videoexitfullscreen',function(){
player.style.width = player.style.height = '';
document.body.classList.remove('fullscreen');
_this.fullScreen = false;
_this.isAdvertShow = true;
_this.isDownLoadShow = true;
_this.playIcon = true;
}, false);
}






player.addEventListener("ended",function(){
_this.playIcon = true;
})
},
openPay(){
this.$router.push({path:'/payList',query:{balance:this.balance}});
},
playVideo(){
let _this = this;
var myVideo = document.getElementById('tcVideo');
myVideo.load();
myVideo.play();
this.isAdvertShow = false;
this.videoScreen = true;
this.fullScreen = !this.fullScreen;
this.playIcon = false;
this.isDownLoadShow = false;
var myPlayIcon = document.getElementById('tcVideoIcon');
myPlayIcon.addEventListener('touchstart',function(event){
if(isAndroidSystem() != true){
if(myVideo.paused){
myVideo.play();
_this.playIcon = false;
_this.fullScreen = true;
_this.isAdvertShow = false;
_this.isDownLoadShow = false;
}else{
myVideo.pause();
_this.playIcon = true;
_this.fullScreen = false;
_this.isAdvertShow = true;
_this.isDownLoadShow = true;
}
}else{
if(myVideo.paused){
myVideo.play();
_this.playIcon = false;
}else{
myVideo.pause();
_this.playIcon = true;
}
}

event.stopPropagation();
}, false);
},
hhhh(){
let _this = this;
var myVideo = document.getElementById('tcVideo');


myVideo.addEventListener('touchstart',function(event){
if(isAndroidSystem() != true){
if(myVideo.paused){
myVideo.play();
_this.playIcon = false;
_this.fullScreen = true;
_this.isAdvertShow = false;
_this.isDownLoadShow = false;
}else{
myVideo.pause();
_this.playIcon = true;
_this.fullScreen = false;
_this.isAdvertShow = true;
_this.isDownLoadShow = true;
}
}else{
if(myVideo.paused){
myVideo.play();
_this.playIcon = false;
}else{
myVideo.pause();
_this.playIcon = true;
}
}

event.stopPropagation();
}, false);

},
refrenshGift(){
this.show3 = false;
this.isGiftLayer = false;
},
handleLike(detail_id){
let params = {
logintoken:this.logintoken,
loginuid:this.loginuid,
params:{
type:this.$route.query.dynamic_type,
detail_id:detail_id,
source:'h5',
}
};
setLike(params).then(res => {
if(res.data.err_msg == 'success'){
this.isZan = !this.isZan;
this.$vux.toast.text(res.data.info,'middle');
this.$emit('refrensh');
}else{
if(res.data.info != 'NotLogin'){
this.$vux.toast.text(res.data.info,'middle');
}
}
})
},
popupGift(){
let params = {
logintoken:this.logintoken,
loginuid:this.loginuid,
};
isLogin(params).then(res => {
if(res.data.err_msg == 'success'){
this.show3 = !this.show3;
}else{
let url = window.location.host;
url = staticData.isHttp + '://' + url + '/#/session/fastLogin'; 
let beforeUrl = window.location.href;   
sessionStorage.setItem('beforeUrl',beforeUrl);
func.wxRedirect(url)
}
})
},
closePopup(){
this.show3 = !this.show3;
},
getCommentList(){
this.show1 = !this.show1;
},
getAttention (userid) {
let params = {
logintoken:this.logintoken,
loginuid:this.loginuid,
params:{
userid:userid
}
};
setAttention(params).then(res => {
if(res.data.err_msg == 'success' && res.data.info == '关注成功！'){
this.isGuanzhu = false;
}
this.$vux.toast.text(res.data.info,'middle');
})
},
openBang(){
this.$router.push({path:'/Ranking',query:{id:this.resData.userid,detail_id:this.resData.id}});
},
openIframe(){
this.$router.push({path:'/introduce',query:{type:2}});
},
alertMsg(){
alert('点击浏览器右上角分享给朋友');
}
}
}



