import {articleLists, hotRank} from "../../../js/service/getData";

export default {
    name: "toggle",
    data() {
        return{
            articleListsParams:{
                cmd:'check',
                params:{
                    page:1,
                    pageSize:5
                }
            },  //活动列表params
            rankParams:{
                cmd:'check',
            },  //热门列表
            isActive:1,
            isActivity:true,
            month_info:[],
            quarter_info:[],
        }
    },
    created() {
        this.initData();
    },
    methods: {
        async initData(){
            let res = await articleLists(this.articleListsParams);
            this.articleData = res.data.data;

            let res1 = await hotRank(this.rankParams);
            this.month_info = res1.data.data.month_info;
            this.quarter_info = res1.data.data.quarter_info;
        },
        onItemClick (detail_id,dynamic_type) {
            this.$router.push({path:'/CompetitionContent',query:{detail_id:detail_id,dynamic_type:dynamic_type}});
            location.reload();
        },
        openNews(id,classid){
            this.$router.push({path:'/NewsDetail',query:{id:id,classid:classid}});
        },
        changeTab(index){
            this.isActive = index;
        },
        linkTo (detail_id) {
            this.$router.push({path:'/NewsDetail',query:{detail_id:detail_id,dynamic_type:6}});
            location.reload();
        },
    }
}



