import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import router from './router/index.js'
import axios from './js/config/axios-config.js'
import Bridge from './js/WebViewBridge'


import ToastPlugin from 'vux/src/plugins/toast/index.js'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import staticData from "./js/config/url-config";

Vue.use(ToastPlugin)
Vue.component('inline-loading', InlineLoading)
Vue.use(VueRouter);









Vue.config.productionTip = false;

Vue.prototype.$ajax = axios;


Vue.prototype.$picUrl = function(str){
return staticData.picurl+str;
};



Vue.prototype.transHtml = function(str){
if(str){
str = str.replace(/&lt;/g,'<');
str = str.replace(/&gt;/g,'>');
str = str.replace(/&amp;/g,'&');
str = str.replace(/\\\\\\"/g,'');
str = str.replace(/\\"/g,'');
}
return str;
};


Vue.prototype.$bridge = Bridge;

new Vue({
router,
render: h => h(App)
}).$mount('#app-box')



