import wx from 'weixin-js-sdk';
import { share } from "../../js/service/getData";
// let src = window.location.href;
export const wxshare = (params,m) =>
    share(params).then(function(res){
        // console.log(res.data.data.appid);
        let data = res.data.data;
        wx.config({
            debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
            appId: data.appid, // 必填，公众号的唯一标识
            timestamp: data.timestamp, // 必填，生成签名的时间戳
            nonceStr: data.noncestr, // 必填，生成签名的随机串
            signature: data.sign,// 必填，签名
            jsApiList: [
                'updateAppMessageShareData',
                'updateTimelineShareData',
            ] // 必填，需要使用的JS接口列表
        });
        wx.ready(function(){
            //分享给朋友
            wx.updateAppMessageShareData({
                title: m.title, // 分享标题
                desc: m.desc, // 分享描述
                link: m.link, // 分享链接
                imgUrl: m.imgUrl, // 分享图标
                // type: '1', // 分享类型,music、video或link，不填默认为link
                // dataUrl: '1', // 如果type是music或video，则要提供数据链接，默认为空
                success: function () {
                    // 用户确认分享后执行的回调函数
                },
                cancel: function () {
                    // 用户取消分享后执行的回调函数
                }
            });
            //分享到朋友圈
            wx.updateTimelineShareData({
                title: m.title, // 分享标题
                desc: m.desc, // 分享描述
                link: m.link, // 分享链接
                imgUrl: m.imgUrl, // 分享图标
                success: function () {
                    // 用户确认分享后执行的回调函数
                },
                cancel: function () {
                    // 用户取消分享后执行的回调函数
                }
            });
        })
    })







