
//创建css
export const createCss = (str) => {
    const s = document.createElement('link');
    s.rel = 'stylesheet';
    s.href = str;
    document.body.appendChild(s);
};

//创建script
export function createScript(str) {
    const s = document.createElement('script');
    s.type = 'text/javascript';
    s.src = str;
    document.body.appendChild(s);
}

/**
 * 取范围内随机数
 * @param Min  最小
 * @param Max   最大
 * @returns {*}
 */
export const randomNum = (Min,Max) => {
    var Range = Max - Min;
    var Rand = Math.random();
    var num = Min + Math.round(Rand * Range);
    return num;
};

/**
 *  转换图片数据格式
 * @param data 数据
 */
export const converPicData = (s) => {
    let data = s;
    for(let i = 0;i < data.length;i++){
        let newarr = '';
        let str = data[i].cover;
        if(str != null){
            if(str.indexOf(',') != -1){ //等于-1代表没查到

                newarr = str.split(',');

                for(let j = 0;j < newarr.length; j++){
                    newarr[j] = newarr[j]+'?x-oss-process=style/squareList';
                }

                if(newarr.length > 3){
                    data[i].cover = newarr.slice(0,3);
                }else{
                    data[i].cover = newarr;
                }
            }else{
                let arr = [];
                if(data[i].dynamic_type == 2){
                    arr[0] = data[i].cover+'?x-oss-process=style/squareList';
                }else{
                    arr[0] = data[i].cover;
                }
                data[i].cover = arr;
            }
        };
    }
    return data;
};

//获取用户本地储存
export const getUserLocalStorage = (that,str) => {
    let params = that.$route.query;

    if(params[str] != undefined){
        return params[str];
    }else{
        let tokens = localStorage.getItem('tokens');
        if(!tokens){
            return null;
        }else{
            let data = JSON.parse(tokens);
            switch (str){
                case 'logintoken':
                    return data.logintoken;
                    break;
                case 'loginuid':
                    return data.loginuid;
                    break;
                default:
                    break;
            }
        }
    }
};

/**
 * 视口的大小，部分移动设备浏览器对innerWidth的兼容性不好，需要
 * document.documentElement.clientWidth或者document.body.clientWidth
 * 来兼容（混杂模式下对document.documentElement.clientWidth不支持）。
 * 使用方法 ： getViewPort().width;
 * @returns {{width: number, height: number}}
 */
export const getViewPort = () => {
    if(document.compatMode == "BackCompat") {   //浏览器嗅探，混杂模式
        return {
            width: document.body.clientWidth,
            height: document.body.clientHeight
        };
    } else {
        return {
            width: document.documentElement.clientWidth,
            height: document.documentElement.clientHeight
        };
    }
};

//获得文档的大小（区别与视口）,与上面获取视口大小的方法如出一辙
export const getDocumentPort = () => {
    if(document.compatMode == "BackCompat") {
        return {
            width: document.body.scrollWidth,
            height: document.body.scrollHeight
        };
    } else {
        return {
            width: Math.max(document.documentElement.scrollWidth,document.documentElement.clientWidth),
            height: Math.max(document.documentElement.scrollHeight,document.documentElement.clientHeight)
        }
    }
};

//截取地址栏网页参数 微信获取code
export const getUrlParams = () => {
    let url = location.search; //获取url中"?"符后的字串
    let theRequest = new Object();
    if(url.indexOf("?") != -1) {
        let str = url.substr(1);
        let strs = str.split("&");
        for(let i = 0; i < strs.length; i++) {
            theRequest[strs[i].split("=")[0]] = unescape(strs[i].split("=")[1]);
        }
    }
    return theRequest;
};

//安卓系统判断
export const isAndroidSystem = () => {
    var u = navigator.userAgent;
    var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
    var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
    if(isAndroid == true){
        return true;
    }else{
        return false
    }
};

/**
 * 转换参数为表单提交类型参数
 * @param arr 类型为json
 * @returns {URLSearchParams}
 */
export const transParamToForm = (arr) => {
    let params = new URLSearchParams();
    for (let key in arr){
        params.append(key,arr[key]);
    }
    return params;
};






