import axios from 'axios'
import qs from 'qs'
import func from '@/js/handleFunction.js'
import router from '../../router/index.js'
import staticData from '../../js/config/url-config'
//import * as _ from '../util/tool'
axios.defaults.timeout = 8000; //响应时间
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'; //配置请求头
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
// axios.defaults.withCredentials = true;

//POST传参序列化(添加请求拦截器)
axios.interceptors.request.use((config) => {
    //在发送请求之前做某件事
    config.headers = {
        'Content-Type':'application/x-www-form-urlencoded'
    }

    // let str = staticData.url4;
    // let url = config.url;
    //
    // if(url.indexOf(str) >=0){ //true 包含
    //
    // }else{
    //     config.data = qs.stringify(config.data);
    // }
    return config;
}, (error) => {
    alert('send error'+error);
    // 对请求错误做些什么
    // return Promise.reject(error);
});

//返回成功状态判断 默认做什么事
axios.interceptors.response.use((res) => {
    //对响应数据做些事

    // let str = '/index.php/dynamic-apply/detail';
    // let url = res.request.responseURL;
    // console.log(url)
    //
    // if(url.indexOf(str) >=0){ //包含这个参数的请求不判断登录跳转
    //     return res;
    // }

    if(res.data.info == '登录失效，请退出重新登录！' || res.data.info == '请登录后操作！'){
        let url = window.location.host;
        url = staticData.isHttp + '://' + url + '/#/session/fastLogin'; //跳转到登录注册页
        let beforeUrl = window.location.href;   //储存当前这个页面
        sessionStorage.setItem('beforeUrl',beforeUrl);
        // console.log(url);
        func.wxRedirect(url)
    }

    return res;
}, (err) => {
    if(err && err.response){
        switch (err.response.status) {
            case 400:
                alert('请求错误');
                break;
            case 403:
                alert('拒绝访问');
                break;
            case 404:
                alert('请求地址出错');
                break;
            case 500:
                alert('服务器内部错误');
                break;
            case 504:
                alert('网关超时-504');
                break;

            default:
        }
    }

});

export default axios;



