
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import {getAcomment,setComment } from "../../js/service/getData";
import staticData from '../../js/config/url-config.js';
import func from '../../js/handleFunction'
export default{
name:'c-footer',
data(){
return {
msg:'',
show:false,
lists:[],
classid:32,
saytext:'',
logintoken:func.getLocalStorage('logintoken'),
loginuid:func.getLocalStorage('loginuid'),
pinglun:0,
}
},
directives:{
TransferDom
},
props:['discuss'],
components:{
Tabbar,
TabbarItem,
PopupHeader,Popup, Flexbox, FlexboxItem,GroupTitle
},
methods:{
goto(){
this.$router.push({path:'/Form1',query:{id:'',userid:''}});
},
sendTalk(){
let _this = this;








let params = {'api':'comment/post','id':this.discuss.id,'classid':this.discuss.classid,'saytext':this.saytext,'logintoken':this.logintoken,'loginuid':this.loginuid};
setComment(params).then(res => {
if(res.data.err_msg == 'success'){

_this.popupMain();
}else{
alert(res.data.info);
}
})
},
popupMain(){
let params = {'api':'comment/list',classid:this.discuss.classid,'id':this.discuss.id};
getAcomment(params).then(res => {
if(res.data.err_msg == 'success'){
this.lists = res.data.data;
this.show = true;
}else{
this.$vux.toast.show({
text:res.data.info
});
}
})
},
openIframe(){
this.$router.push({path:'/introduce',query:{type:2}});
},
}
}



