
import foot from '@/components/footer/footer.vue';
import heads from '@/components/header/header.vue';
import {adverList} from "@/js/service/getData";
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import Card from 'vux/src/components/card/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'

import func from "../../js/handleFunction";
import {setLike, squareList} from "../../js/service/getData";
import {converPicData, getUserLocalStorage} from "../../js/func";
export default {
name: 'Home',
created(){
this.initData();
},
directives:{
TransferDom
},
data(){
return{
user:'',
index: 0,
imgPlaceHolder:'http://greattone.oss-cn-shanghai.aliyuncs.com/images/placeholder.png?x-oss-process=style/pc-userpic',
swipeList:[],//主轮播图列表
subGuang:false, //音乐广场列表下子列表显示
squareLists:[],
lists:[],
scrollTimer:'',
adverParams:{
cmd: 'check',
params: {
type: '305030e2-c053-a166-24e0-44f9b5d53713',
owner_id: '',
location: '0537ca58-9dd0-5a96-a4d9-bd88b6e3ae7c',
}
},
squareParams:{
loginuid:func.getLocalStorage('loginuid'),
logintoken:func.getLocalStorage('logintoken'),
cmd:'check',
params:{
page:1,
pageSize:10
}
},
}
},
components:{
XHeader,Search,foot,heads, Drawer, ViewBox,Swiper,SwiperItem, Grid, GridItem,Tab,TabItem, Popup,Checker,CheckerItem,ToastPlugin,Card,Divider,XButton,Flexbox,FlexboxItem,XImg
},
mounted(){






window.addEventListener('scroll',this.handleScroll);
},
destroyed(){
window.removeEventListener('scroll',this.handleScroll);
},
methods:{
async initData() {
let res,res1;

res = await adverList(this.adverParams);    //广告
res1 = await squareList(this.squareParams);     //动态

this.squareLists = converPicData(res1.data.data);
res = res.data.data;
res.forEach( (item,index) => {
let obj = {};
obj.url = item.advert_href;
obj.img = item.advert_url;
this.swipeList.push(obj);
});
},

async nextPageLoad() {
let res;
this.squareParams.params.page++;
res = await squareList(this.squareParams);
res = res.data.data;
res.forEach((item,index) => {
this.squareLists.push(item);
})

},

handleScroll() {
this.scrollTimer = func.upglideScroll(this.scrollTimer,this.nextPageLoad);
},
handleLike(dynamic_type,detail_id,userid) {
func.handleLike(dynamic_type,detail_id).then(res => {
if(res.data.info =='点赞成功！'){
this.squareLists.forEach((item,index) => {
if(this.squareLists[index].user_id == userid){
this.squareLists[index].like_num++;
}
})
}else{
this.squareLists.forEach((item,index) => {
if(this.squareLists[index].user_id == userid){
this.squareLists[index].like_num--;
}
})
}
})

},

onItemClick (value) {
let params;
switch (value){
case '全部动态':
params = this.getGuangList('info/guanlist',1,1,20,'','','');
this.httpPost(params);
break;
case '音乐新闻':
this.$router.push({path:'/News'});
break;
case '推荐贴':
params = this.getGuangList('info/guanlist',1,1,20,'','onclick','');
this.httpPost(params);
break;
case '关注动态':
params = this.getListFriend('zhiyin/list',1,20,0);
this.httpPost(params);
break;
case '知音动态':
params = this.getListFriend('zhiyin/list',1,20,1);
this.httpPost(params);
break;
case '年热帖':
params = this.getGuangList('info/guanlist',1,1,20,'',4,'');
this.httpPost(params);
break;
case '季热帖':
params = this.getGuangList('info/guanlist',1,1,20,'',5,'');
this.httpPost(params);
break;
case '月热帖':
params = this.getGuangList('info/guanlist',1,1,20,'',3,'');
this.httpPost(params);
break;
case '周热帖':
params = this.getGuangList('info/guanlist',1,1,20,'',2,'');
this.httpPost(params);
break;
}



},
mainMenuPicke(index){
this.sulistShow();
switch(index){
case 0:this.subGuang = true;
break;
case 1:this.subGuang = true;
break;
case 2:this.subGuang = true;
break;
}
},
sulistHide(e){
document.querySelector('.main-sublist-tab').className += " "  +'sublist-tab-hide';
e.target.className += " " + 'list-cover-hide';
},

sulistShow(){
let sublistTab = document.querySelector('.main-sublist-tab');
sublistTab.className = sublistTab.className.replace(/sublist\-tab\-hide/g,' ');
let cover = document.querySelector('.list-cover');
cover.className = cover.className.replace(/list\-cover\-hide/g,' ');
},

getListFriend(api,pageIndex,pageSize,iszhiyin){
let params = new URLSearchParams();
params.append('api',api);
params.append('pageIndex',pageIndex);
params.append('pageSize',pageSize);
params.append('iszhiyin',iszhiyin);
params.append('loginuid',this.user.userid);
params.append('logintoken',this.user.token);
return params;
},

},
}



