let windowUrl = window.location.href;

let u1 = 'http://h5.yinyueriji.net';
let u2 = 'localhost';//本地
let u3 = 'https://h5.yinyueriji.net';
let u4 = 't-h5.yinyueriji.net';//测试

let url, isHttp, openWx;

if (windowUrl.indexOf(u1) >= 0 || windowUrl.indexOf(u3) >= 0) { //true 包含 //正式
    url = 'https://webapi.yinyueriji.net';
    isHttp = 'https';
    openWx = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=';
} else if (windowUrl.indexOf(u2) >= 0) { // 本地
    url = 'https://t-webapi.yinyueriji.net';
    isHttp = 'http';
    openWx = 'https://h5.yinyueriji.net/get-weixin-code.html?appid=';
} else if (windowUrl.indexOf(u4) >= 0) { //测试
    url = 'https://t-webapi.yinyueriji.net';
    isHttp = 'https';
    openWx = 'https://h5.yinyueriji.net/get-weixin-code.html?appid=';
} else { //其他
    url = 'https://t-webapi.yinyueriji.net';
    isHttp = 'http';
    openWx = 'https://h5.yinyueriji.net/get-weixin-code.html?appid=';
}


export default {
    picurl: '/static/',
    url: url,
    isHttp: isHttp,
    openWx: openWx,
};







