
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import {
adverList,
articleDetail,
articleLists,
commentList, setAttention,
setComment,
setLike,
setLook
} from "@/js/service/getData";
import AdvertLayer from '@/components/advert/advert'
import func from '@/js/handleFunction';
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import {wxshare} from "../../../js/wx/share";
import Cnzz from '@/components/cnzz/index'
export default {
name: 'hello',
data () {
return {
isFocus:true,
classid:null,
loadMore:false,
id:null,
attentionStatus:false,
url:'',
saytext:'',
params:{
logintoken:func.getWindowLocalStorage(this,'logintoken'),
loginuid:func.getWindowLocalStorage(this,'loginuid'),
cmd:'check',
params:{
detail_id:'',
}
},
dataDetail:'',
commentData:'',
pull_end:true,
userid:'',
articleListsParams:{
cmd:'check',
params:{
page:1,
pageSize:3
}
},  //活动列表params
articleData:'',
areaData:'',
commentParams:{
logintoken:func.getWindowLocalStorage(this,'logintoken'),
loginuid:func.getWindowLocalStorage(this,'loginuid'),
params:{
detail_id:this.$route.query.detail_id,
type:this.$route.query.dynamic_type,
page:1,
pageSize:5
}
},
setParams:{
logintoken:func.getWindowLocalStorage(this,'logintoken'),
loginuid:func.getWindowLocalStorage(this,'loginuid'),
params:{
detail_id:this.$route.query.detail_id,
parent_id:'',
content:'',
source:'h5',
type:this.$route.query.dynamic_type,
source_type:this.isAppRequest(),
}
},
}
},
components:{
Flexbox,FlexboxItem,AdvertLayer,Cnzz
},
created(){
this.initData();
},
watch:{
commentData: {
handler(val, oldVal){
if(this.commentData.info.length == 5){
this.loadMore = true;
}
},
deep:true,
},
areaData:{
handler(val, oldVal){
let dom = document.querySelector('.area');

if(dom.scrollTop > 0){
dom.rows++;
dom.style.height = 'auto';
}
if(val == ''){
dom.rows = 1;
}
},
}
},
methods:{
async initData(){
let look = {
type:this.$route.query.dynamic_type,
detail_id:this.$route.query.detail_id,
source:'h5',
};
setLook(func.trimParamsWindow(this,look));
this.params.params.detail_id = this.$route.query.detail_id;
let data = await articleDetail(this.params);
this.dataDetail = data.data.data;
document.title = this.dataDetail.title;
this.userid = this.dataDetail.publish_id;

if(this.dataDetail.is_fans == 2){
this.attentionStatus = true;
}

let adParams = {
cmd: 'check',
params: {
type: '86d7437e-58a7-f35d-3e25-abbd897b5314',
owner_id: this.dataDetail.publish_id,
location: 'cedf7d0f-0311-a4cb-9b4d-ecc7ffa2b426,ab4b6640-fcec-46b1-43f3-56f800a6320d',
}
}
let res1 = await adverList(adParams); //广告
this.adInfo = res1.data.data;

let arl = await articleLists(this.articleListsParams);
this.articleData = arl.data.data;

this.getCommentList();


let url = window.location.href;
let p = {
current_url:url
};
let m = {
link:url,
title:this.dataDetail.title,
desc:'来自 @' + this.dataDetail.publish_name + ' 的音乐日记',
imgUrl:this.dataDetail.cover_pic,
};

wxshare(p,m).then(function (res) {

});
},

async getCommentList(){
let comData = await commentList(this.commentParams);
if(comData.data.err_msg == 'success'){
this.commentData = comData.data.data;
this.commentData.info.length !=5 ? this.pull_end = true:this.pull_end = false;
}else{
this.pull_end = true;
}
},
linkTo (detail_id) {
this.$router.push({path:'/NewsDetail',query:{detail_id:detail_id,dynamic_type:6}});
location.reload();
},

clickLoadMore(){
this.commentParams.params.page++;
commentList(this.commentParams).then(res => {
if(res.data.err_msg == 'error'){
this.pull_end = true;
}else{
for (let i = 0; i < res.data.data.info.length; i++) {
this.commentData.info.push(res.data.data.info[i]);
}
}
});
},

sendComment(){
if(this.areaData == ''){
return false;
}
this.setParams.params.content = this.areaData;
setComment(this.setParams).then(res => {
if(res.data.err_msg == 'success'){
this.areaData = '';
this.commentParams.params.page = 1;
this.getCommentList();
}else{

}
})
},

handleLike(){
let params = {
type:this.$route.query.dynamic_type,
detail_id:this.dataDetail.detail_id,
source:'h5',
source_type:this.isAppRequest(),
};
setLike(func.trimParamsWindow(this,params)).then(res => {
if(res.data.err_msg == 'success'&&res.data.info == '点赞成功！'){
this.dataDetail.like_num++;
alert(res.data.info);
}else if(res.data.err_msg == 'success'&&res.data.info == '取消点赞成功！'){
this.dataDetail.like_num--;
alert(res.data.info);
}
})
},

isAppRequest(){
let params = this.$route.query;
if(params['logintoken'] != undefined){
return 1;
}else{
return '';
}
},

handleAttention () {
let params = {
userid:this.dataDetail.publish_id,
source_type:this.isAppRequest(),
};

setAttention(func.trimParamsWindow(this,params)).then(res => {
if(res.data.err_msg == 'success'){
this.attentionStatus = false;
alert(res.data.info);
}else{
if(res.data.info == '不能关注自己！'){
alert(res.data.info);
}
}
})
},

getFocus(){
this.isFocus = false;
}
}
}




