import {randomNum} from "../../../js/func";
import {giveReward} from "../../../js/service/getData";
import staticData from '../../../js/config/url-config'
import func from "../../../js/handleFunction";
import {wxpay} from "../../../js/wx/pay";

export default {
    name: "give-reward",
    data(){
        return{
            defaultText:[5,20,50,80,100],   //默认列表
            defaultCustom:'自定义',    //默认自定义文字
            borderIndex:-1,     //选中下标
            num:randomNum(1,10),    //随机数字
            moneyCount:'',  //输入金额
            btnActive:false,    //确定按钮状态
        }
    },
    props:{
        userInfo:{
            type:Object,
            require:true,
        }
    },
    methods:{
        changeNum(){
            this.num = randomNum(1,10);
        },
        closeDialog(){
            this.$emit('close')
        },
        handleBorderLight(index){
            this.btnActive = true;
            this.borderIndex = index;
        },
        changeCount(){
            // console.log(this.$refs.input.focus())
            this.$refs.input.focus();
            this.handleBorderLight(5);
            this.defaultCustom = '';
        },
        submitGift(){
            let openid = localStorage.getItem('openid');
            if(openid == '' || openid == null || openid == undefined || openid == 'undefined'){
                let url = window.location.host;
                url = staticData.isHttp + '://' + url + '/#/session/fastLogin'; //跳转到登录注册页
                let beforeUrl = window.location.href;   //储存当前这个页面
                sessionStorage.setItem('beforeUrl',beforeUrl);
                func.wxRedirect(url)
            }else{
                if(this.defaultText[this.borderIndex] == undefined && this.moneyCount == ''){
                    alert('请选择金额哦');
                    return false;
                }

                let params = {
                    loginuid:func.getLocalStorage('loginuid'),
                    logintoken:func.getLocalStorage('logintoken'),
                    params:{
                        money:this.defaultText[this.borderIndex] || this.moneyCount,
                        pay_type:2, //微信
                        source:'h5',
                        detail_id:this.$route.query.detail_id,
                        dynamic_type:this.$route.query.dynamic_type,
                        accept_id:this.userInfo.userid,
                        return_url:window.location.href,
                        openid:openid,
                    }
                };

                wxpay(params).then(function (res) {

                });
            }
        }
    }
}



