
import foot from '../../../components/footer/comItem-foot.vue';
import {applyPay, comInfo, comItem, isLogin, toPay} from "../../../js/service/getData";
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import func from '../../../js/handleFunction.js'
import staticData from '../../../js/config/url-config'
import {wxshare} from "../../../js/wx/share";
import wx from 'weixin-js-sdk';
export default {
name: 'index',
mounted(){
window.addEventListener('scroll',this.handleScroll);
},
destroyed(){
window.removeEventListener('scroll',this.handleScroll);
},
directives:{
TransferDom
},
data(){
return{
defText:'我要报名',
isOver:1,   //1进行中 2未开始 3已结束
user:'',
resData:[],
id:'',
userid:'',
pageIndex:1,
pageSize:10,
lists: [],
pullupConfig2: {
content: '上拉加载更多',
downContent: '松开进行加载',
upContent: '上拉加载更多',
loadingContent: '加载中...'
},
defaultImg:"https://greattone4.oss-cn-shanghai.aliyuncs.com/h5/default/2018062210150549.jpg",
classid:'',
loadImg:true,
params:{
cmd:'achieve',
logintoken:func.getLocalStorage('logintoken'),
loginuid:func.getLocalStorage('loginuid'),
params:{
act_id:this.$route.query.act_id, //活动id
}
},  //活动详情params
dynamicParams:{
loginuid:'',
logintoken:'',
params:{
act_id:this.$route.query.act_id, //活动id
page:1,
pageSize:12,
}
},  //活动报名列表
isTabActive:true, //活动介绍tab
scrollTimer:'', //上拉加载
activity_type_id:''
}
},
created(){
this.initData();
},
components:{
XHeader,Search,Drawer,ViewBox,Scroller,foot,Tabbar, TabbarItem,
},
methods:{
handleScroll(){
if(!this.isTabActive){
this.scrollTimer = func.upglideScroll(this.scrollTimer,this.loadList);
}
},

async initData(){
let res,res1;

res = await comInfo(this.params);
document.title = res.data.data.name;
this.resData = res.data.data;
this.activity_type_id = res.data.data.activity_type_id;

this.loadImg = false;
if(this.resData.is_pay == 2){
this.defText = '去支付';
}else if(this.resData.is_pay == 3){
this.defText = '已报名';
}
this.isOver = this.resData.is_over;

res1 = await comItem(this.dynamicParams);

if(res1.data.err_msg == 'success'){
this.lists = func.converData(res1.data.data.list);
}

let url = window.location.href;
let p = {
current_url:url
};
let m = {
link:url,
title:this.resData.name,
desc:'来自 @' + this.resData.user_name + ' 的音乐日记',
imgUrl:this.resData.cover_pic,
};

wxshare(p,m).then(function (res) {

});
},

async loadList () {
let res,lists;

this.dynamicParams.params.page++;
res = await comItem(this.dynamicParams);
if(res.data.err_msg == 'success'){
lists = func.converData(res.data.data.list);
lists.forEach((item,index) => {
this.lists.push(item);
})
}
},
onItemClick (detail_id,dynamic_type) {
this.$router.push({path:'/CompetitionContent',query:{detail_id:detail_id,act_id:this.resData.act_id,dynamic_type:dynamic_type}});
location.reload();
},
changeTab($event){
let classname = $event.currentTarget.className;
if(classname == ''){
this.isTabActive = !this.isTabActive;
}
},
goApply(){

if(this.resData.is_pay == 1){
let params = {
logintoken:func.getLocalStorage('logintoken'),
loginuid:func.getLocalStorage('loginuid'),
};
isLogin(params).then(res => {
if(res.data.err_msg == 'success'){
this.$router.push({path:'/ActivityForm',query:{act_id:this.$route.query.act_id,activity_type:this.activity_type_id}});
}else{
let url = window.location.host;
url = staticData.isHttp + '://' + url + '/#/session/fastLogin'; 
let beforeUrl = window.location.href;   
sessionStorage.setItem('beforeUrl',beforeUrl);
func.wxRedirect(url)
}
})
}else if(this.resData.is_pay == 2){
let p = {
logintoken:func.getLocalStorage('logintoken'),
loginuid:func.getLocalStorage('loginuid'),
params:{
activity_id:this.resData.act_id,
}
};
toPay(p).then(res => {
this.applyPay(res.data.data);
})
}

},
applyPay(data){
let openid = localStorage.getItem('openid');
let p = {
logintoken:func.getLocalStorage('logintoken'),
loginuid:func.getLocalStorage('loginuid'),
params:{
pay_type:2,
pay_platform:1,
goodsSystem_id:data.goodsSystem_id,
order_no:data.order_no,
activity_id:data.activity_id,
openid:openid,
return_url:window.location.href,
}
};
applyPay(p).then(res => {
if(res.data.err_msg == 'success'){
let data = res.data.data;
wx.config({
debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
appId: data.appid, // 必填，公众号的唯一标识
timestamp: data.timestamp, // 必填，生成签名的时间戳
nonceStr: data.noncestr, // 必填，生成签名的随机串
signature: data.sign,// 必填，签名
jsApiList: [
'chooseWXPay'
] // 必填，需要使用的JS接口列表
});
wx.ready(function(){
wx.chooseWXPay({
timestamp: data.timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
nonceStr: data.noncestr, // 支付签名随机串，不长于 32 位
package: data.packageValue, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
signType:data.signtype, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
paySign:data.sign, // 支付签名
success: function (res) {

},
fail: function(res){
alert('支付失败');
}
});
})
}else{
alert('支付失败')
}
});
}
},
}



