import func from '../../../js/handleFunction';
import { swiper, swiperSlide } from 'vue-awesome-swiper';
import staticData from '../../../js/config/url-config';
import 'swiper/dist/css/swiper.css'
export default {
    name: "gift",
    data(){
        return{
            isGiftShow:false,   //loading
            giftList:[],    //礼物列表
            defName:'未选择',  //默认显示为选择
            shade:-1,     //列表选中下表
            defPrice:null,  //列表默认价格
            balance:0,  //爱乐币余额
            params:{
                loginuid:func.getLocalStorage('loginuid'),
                logintoken:func.getLocalStorage('logintoken'),
                params:{
                    activity_id:'2f0fc929-58cd-bbc5-403a-0c6d7d18c376'
                }
            },  //发送数据包
            params1:{
                loginuid:func.getLocalStorage('loginuid'),
                logintoken:func.getLocalStorage('logintoken'),
                params:{
                    gift_id:'',
                    num:1,
                    accept_id:this.resData.userid,
                    detail_id:this.resData.id,
                }
            },  //发送数据包1

            swiperOption: {
                notNextTick: true,
                autoplay: false,
                pagination: {
                    el: '.swiper-pagination',
                },
            },
        }
    },
    components:{
        swiper,swiperSlide,
    },
    props:{
        resData:{
            type:Object,
            required:true,
        }
    },
    //定义这个sweiper对象
    computed: {
        swiper() {
            return this.$refs.mySwiper.swiper;
        }
    },
    created(){
        this.getGiftList();
    },
    methods:{
        getGiftList(){
            let _this = this;
            this.$ajax.post(staticData.url + 'index.php?r=activity-gift/index',this.params).then(res => {
                // _this.show3 = !this.show3;
                _this.isGiftShow = true;
                _this.giftList = res.data.data.gifts;
                _this.balance = res.data.data.balance;
            }, res => {

            });
        },
        selectItem(id,index,name,price){
            this.params1.params.gift_id = id;
            this.shade = index;
            this.defName = name;
            this.defPrice = price;
        },
        linkTo(){
            // this.$router.push({path:'/payList',query:{balance:this.balance}});
        },
        submitGift(){
            if(this.defPrice == null){
                this.$vux.toast.text('选个礼物吧','middle');
            }else{
                if(this.balance >= this.defPrice){
                    this.$ajax.post(staticData.url + 'index.php?r=record-gift/create',this.params1).then(res => {
                        this.$emit('refrenshGift');
                        this.$vux.toast.text(res.data.info,'middle');
                    }, res => {

                    });
                }else{
                    // this.$router.push({path:'/payList',query:{balance:this.balance}});
                }
            }
        }
    }
}



