
import func from '../../../js/handleFunction'
import { setVote,isVote } from '../../../js/service/getData'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
name: "vote",
data(){
return{
show:false,     //投票滑动弹出层
logintoken:func.getLocalStorage('logintoken'),
loginuid:func.getLocalStorage('loginuid'),
countDown:'',       //倒计时
countProgress:0,      //倒计时进度条
}
},
directives:{
TransferDom
},
mounted(){
/* 动态加载js，没有模块化的插件可以这样加载 */
const s = document.createElement('script');
s.type = 'text/javascript';
s.src = 'https://cstaticdun.126.net/load.min.js';
document.body.appendChild(s);

},
props:{
resDetail:{
type:Object,
required:true,
},
resData:{
type:Object,
required:true,
}
},
components:{Popup,TransferDom,GroupTitle,Group},
created(){
setTimeout(() =>{
this.countDownProgress();
},1000)

},
methods:{
async countDownProgress(){
console.log(this.resDetail.start_time+'/'+this.resData.browse_num)
if(this.resDetail.start_time || this.resDetail.end_time){
let current = func.countTime(this.resDetail.start_time,this.resDetail.end_time,0);
if(current.isEnd == 0){
this.countDown = '未开始';
this.countProgress = 0;
}else if(current.isEnd == 1){
this.countDown = '已结束';
this.countProgress = 100;
}else{
let totalTime = func.countTime(this.resDetail.start_time,this.resDetail.end_time,1);
let progress = (1-(current.d/totalTime.d))*100;
progress = Math.ceil(progress);

this.countDown = current.d + '天' + current.h + '时' + current.m + '分';
this.countProgress = progress;
}
}
},
popupVote(){
let _this = this;
_this.show = !this.show;
initNECaptcha({
/* 网易云顿滑动验证，config对象，参数配置 */
captchaId: 'a8b9aa6a8d854a0e9d975e93f358cf1b',
element: '#captcha',
mode: 'embed',
width: '320px',
onVerify:function(err,ret){
if(ret.validate){
let params = {
logintoken:_this.logintoken,
loginuid:_this.loginuid,
params:{
detail_id:_this.$route.query.detail_id,
act_id:_this.$route.query.act_id,
captcha_code:ret.validate,
}
};
setVote(params).then(res => {
_this.show = !_this.show;
if(res.data.err_msg == 'success'){
_this.$vux.toast.text(res.data.info,'middle');
_this.$emit('upRefrensh');
}else{
if(res.data.info != 'NotLogin'){
_this.$vux.toast.text(res.data.info,'middle');
}
}
})
}

}
}, function (instance) {

}, function (err) {

})












},
}
}



