
import Scroller from 'vux/src/components/scroller/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { roomLists } from "../../js/service/getData";

export default {
name:'Home',
data () {
return {
pageIndex:1,
pageSize:10,
lists: [],
titleRight:'历史回顾',
pullupConfig2: {
content: '',
downContent: '',
upContent: '',
loadingContent: '加载中...'




},
show1:true,
params:{
cmd:'check',
params:{
page:1,
pageSize:8
}
},  //活动列表params
}
},
directives:{
TransferDom
},
components: {
Scroller,Divider,Spinner,XHeader,Drawer,ViewBox,Loading
},
created(){
this.initData();
},
methods: {
initData(){
roomLists(this.params).then(res => {
this.show1 = false;
this.lists = res.data.data.list;
})
},


loadList () {
this.params.params.page++;
let _this = this;
_this.$nextTick(() => { _this.$refs.cPull.reset();});

roomLists(this.params).then(res => {
if(res.data.data.list.length){
for (let i = 0; i < res.data.data.list.length; i++) {
_this.lists.push(res.data.data.list[i]);
}

setTimeout(() => {

_this.$refs.cPull.donePullup();
}, 100);
}
})
},
onItemClick (store_id) {
this.$router.push({path:'/RoomItem',query:{user_id:store_id}});
},
}

}



