
import Divider from 'vux/src/components/divider/index.vue'

import func from '../../../js/handleFunction.js';
import videoLayer from '../activity_modules/wxVideo';
import picLayer from '../activity_modules/wxPictrue';
import {comIteminfo, comItem, shortDynamicDetail} from "../../../js/service/getData";
import 'swiper/dist/css/swiper.css'
import AdvertLayer from '../../../components/advert/advert'
import {getDocumentPort, getUserLocalStorage, getViewPort} from "../../../js/func";
import Toggle2 from '../activity_modules/toggle_2nav'

export default {
name: 'ListItemContent',
data(){
return{
id:'',//活动id
classid:'',//活动类型id
itemid:'',//当前播放视频的id
resData:{},//视频数据
lists:[],//列表数据
pageSize:10,//一页几条
pageIndex:1,//第几页
fullScreen:true,//wx是否全屏 默认false
isActive:true,
newsLists:[],
swipeList:[],
swiperOption: {
notNextTick: true,
autoplay: true,
direction : 'horizontal',
},
userinfo:{},//帖子人信息
level:{},//帖子人等级信息
htmlShow:false,
params:{
cmd:'check',
loginuid:getUserLocalStorage(this,'loginuid'),
logintoken:getUserLocalStorage(this,'logintoken'),
params:{
detail_id:this.$route.query.detail_id, //动态id
}
},  //活动报名详情
params1:{
loginuid:'',
logintoken:'',
params:{
act_id:'', //活动id
page:1,
pageSize:12,
}
},  //活动报名列表
resDetail:{},
owner_id:'',
callData:false,
}
},
route: {
canReuse: false,
},

mounted(){

},
created(){
this.initData();
},
computed: {
dyType(){
let type = this.$route.query.dynamic_type;
if(type == 4 || type == 1){
return true;
}else if(type == 5 || type == 2){
return false;
}
},
isActivity() {
if(this.$route.query.dynamic_type == 4 || this.$route.query.dynamic_type == 5){
return true;
}
}
},
components:{
videoLayer,Divider,AdvertLayer,picLayer,Toggle2
},
methods: {
async initData(){
let type = this.$route.query.dynamic_type;
if(type == 4 || type == 5){

let data = await comIteminfo(this.params);
let res = data.data.data;
document.title = res.userInfo.username + ' 的音乐日记';
this.resData = res.dynamicInfo;
this.resDetail = res;
this.userinfo = res.userInfo;
this.level = '一级';
this.htmlShow = true;
this.owner_id = res.owner_id || res.userInfo.userid;
this.callData = true;

}else if(type == 1 || type == 2){
let data = await shortDynamicDetail(this.params);
let res = data.data.data;
document.title = res.userInfo.username + ' 的音乐日记';
this.resData = res.dynamicInfo;
this.resDetail = res;
this.userinfo = res.userInfo;
this.level = '一级';
this.htmlShow = true;
this.owner_id = res.owner_id || res.userInfo.userid;
this.callData = true;

}

},

handleScroll(e){
let view = getViewPort();
let docPort = getDocumentPort();
let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;

if (scrollTop + view.height >= docPort.height) {
this.loadList();
}
},
async initList(){
let data1 = await comItem(this.params1);
this.lists = func.converData(data1.data.data.list);
},

async loadList () {
this.params1.params.page++;
let res = await comItem(this.params1);


for (var i = 0; i < res.data.data.list.length; i++) {
this.lists.push(res.data.data.list[i]);
}
},

}

}




