
import foot from '@/components/footer/comItem-foot.vue';
import staticData from '@/js/config/url-config.js';
import {getRoomItem, comItem, teacherDetail} from "../../../js/service/getData";
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Scroller from 'vux/src/components/scroller/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Panel from 'vux/src/components/panel/index.vue'

import DownLoad from '@/components/header/download'
import shareFoot from '@/components/footer/shareFoot'
import {createCss} from "@/js/func";
import { swiper, swiperSlide } from 'vue-awesome-swiper';
export default {
name: 'RoomItem',
mounted(){
createCss('https://greattone4.oss-cn-shanghai.aliyuncs.com/h5/css/mui.min.css')
},
directives:{
TransferDom
},
data(){
return{
swiperOption1: {
notNextTick: true,
autoplay: false,
direction : 'horizontal',
pagination: {
el: '.swiper-pagination',
},
},
url1:'https://www.greattone.net/app/bai_1.php?classid=120&id=4155',
user:'',
resData:[],
id:'',
userid:'',
pageIndex:1,
pageSize:10,
lists: [],
pullupConfig2: {
content: '上拉加载更多',
downContent: '松开进行加载',
upContent: '上拉加载更多',
loadingContent: '加载中...'
},
defaultImg:"/static/bs_place.jpg",
swipeList:[],
lists1:[],
lists2:[],
list1:[],
params:{
cmd:'check',
params:{
user_id:this.$route.query.user_id,
}
},  //活动详情params
teacherDetail:{
course_info:{
count:0
},
store_lease:{
count:0
},
store_activity:{
count:0
},
dynamic_info:{
number:0
},
introduce:'',
introduce_video:{
count:0
},
user_info:{
photos:[],
},
},
swipePhoto:[],
}
},
components:{
swiper, swiperSlide,
XHeader,Search,Drawer,ViewBox,Scroller,foot,Swiper,SwiperItem,Group,Panel,DownLoad,shareFoot
},
created(){
this.initData();
},
methods:{
async initData(){
let res = await teacherDetail(this.params);
this.teacherDetail = res.data.data;
document.title = this.teacherDetail.user_info.name || '';
this.swipePhoto = this.converData(res.data.data.user_info.photos);

},
converData(str){
if(str != ''){
let arr = [];
for(let i = 0;i < str.length;i++){
let obj = {};
obj.url = '';
obj.img = str[i];
arr.push(obj);
}
return arr;
}
},
linkTo(detail_id){
this.$router.push({path:'/CompetitionContent',query:{detail_id:detail_id,dynamic_type:1}});
},
hasLogin(){
let userdata = sessionStorage.getItem('userdata');
if(!userdata){
this.$router.replace('/session/login');
}else{
let data = JSON.parse(userdata);
this.logintoken = data.token;
this.loginuid = data.userid;
}
},

async initList(){
let params = {'api':'user/getuserInfo','userid':this.userid,'extra':'photo','logintoken':this.logintoken,'loginuid':this.loginuid};
let data = await getRoomItem(params);
this.lists = data.data.data;

let str = this.lists.photo;
let userpic = this.lists.userpic;
let strs = str.split("::::::");
let obj = {};
obj.img = userpic;
this.swipeList.push(obj);

for (let i = 0;i<strs.length-1;i++){
let obj = {};
obj.img = strs[i];
this.swipeList.push(obj);
}

let obj1 = {};
obj1.title = this.lists.address+this.lists.address1+this.lists.address2;
let obj2 = {};
obj2.title = this.lists.telephone;
this.list1.push(obj1);
this.list1.push(obj2);
},

loadList () {
this.pageIndex++;
let params = {'api':'info/hxvotelist','hai_id':this.id,'pageIndex':this.pageIndex,'pageSize':this.pageSize,'extra':'tou_num,hai_petition,hai_video,hai_name,hai_photo'};

comItem(params).then(res => {
for (var i = 0; i < res.data.data.length; i++) {
this.lists.push(res.data.data[i]);
}
})

},
onItemClick (classid,itemid) {
this.$router.push({path:'/CompetitionContent',query:{classid:classid,id:this.id,itemid:itemid}});
},
openList(){
this.$refs.child.popupMain();
}
},
}



