
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Toast from 'vux/src/components/toast/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import {
isVote,
setAttention,
setLike,
setVote,
isLogin,
comIteminfo,
shortDynamicDetail
} from "../../../js/service/getData"
import { swiper, swiperSlide } from 'vue-awesome-swiper';
import 'swiper/dist/css/swiper.css'
import CommentLayer from './comment'
import GiftLayer from './gift'
import GiveReward from './giveReward'
import DownLoad from '../../../components/header/download'
import func from '../../../js/handleFunction'
import VoteLayer from './vote'
import Vue from 'vue'
import {wxshare} from "../../../js/wx/share";
import Cnzz from '../../../components/cnzz/index'
import staticData from '../../../js/config/url-config'
export default {
name: "wx-layer",
data(){
return{
show:false,
show1:false,
show2:false,
show3:false,
show4:false,
toastValue:false,
isGiftLayer:false,
shade:-1,
toastText:'',
loading:true,

isDownLoadShow:true,    //头部下载app显示
defName:'未选择',
defPrice:null,
defGuan:'+ 关注',
position: 'default',
pageIndex:1,
pageSize:20,
lists:[],
saytext:'',
logintoken:func.getLocalStorage('logintoken'),
loginuid:func.getLocalStorage('loginuid'),
fullScreen:false,//wx是否全屏 默认false
playIcon:false,
activity_id:'',//活动id
swiperOption1: {
notNextTick: true,
autoplay: false,
direction : 'horizontal',
pagination: {
el: '.swiper-pagination',
},
},
swiperOption: {
notNextTick: true,
autoplay: false,




pagination: {
el: '.swiper-pagination',
},
},
giftList:[],//礼物列表
balance:0,//爱乐币余额
giftid:null,//礼物id
zanNum:0,

isCommentShow:false,
classid:'',
countDown:'',       //倒计时
countProgress:20,      //倒计时进度条
params:{
cmd:'check',
loginuid:func.getLocalStorage('loginuid'),
logintoken:func.getLocalStorage('logintoken'),
params:{
detail_id:this.$route.query.detail_id, //动态id
}
},  //活动报名详情
resDataPic:[],
resData:{},
userinfo:{},
resDetail:{},
}
},
watch:{
show1(val,oldVal){
if(val == true){
document.body.style['width'] = '100%';
document.body.style['overflow'] = 'hidden';
document.body.style['position'] = 'fixed';
}else{
document.body.style['overflow'] = 'auto';
document.body.style['position'] = 'relative';
}
},
},
directives:{
TransferDom
},
components:{
CommentLayer,GiftLayer,DownLoad,VoteLayer,GiveReward,Cnzz,
Popup,Toast,Grid,GridItem,GroupTitle,Group,Cell,Flexbox,FlexboxItem,XInput,XTextarea,PopupHeader,XButton,
swiper,swiperSlide,InlineLoading,XHeader,Swiper,SwiperItem,XDialog
},
props:{

},
updated(){

},

computed: {

isActivity() {
if(this.$route.query.dynamic_type == 5){
return true;
}
},
zanFlag(){
return this.resDetail.is_like == 1?true:false;
},
isGuanzhu(){
return this.resDetail.is_fans == 1?false:true;
}
},
mounted(){
const s = document.createElement('script');
s.type = 'text/javascript';
s.src = 'https://cstaticdun.126.net/load.min.js';
document.body.appendChild(s);

},
beforeMount(){

},
created(){



this.initData();
},
methods:{
async initData(){
let res;
if(this.$route.query.dynamic_type == 5){
res = await comIteminfo(this.params);
}else{
res = await shortDynamicDetail(this.params);
}

this.resData = res.data.data.dynamicInfo;
this.userinfo = res.data.data.userInfo;
this.resDataPic = this.converData(res.data.data.dynamicInfo.photo);
this.resDetail = res.data.data;


let url = window.location.href;
let p = {
current_url:url
};
let m = {
link:url,
title:this.userinfo.username + '的音乐日记',
desc:this.resData.content,
imgUrl:this.userinfo.head_pic,
};

wxshare(p,m).then(function (res) {

});
},

converData(str){
if(str != ''){
let newarr = str.split(',');
let arr = [];
for(let i = 0;i < newarr.length;i++){
let obj = {};
obj.url = '';
obj.img = newarr[i];
arr.push(obj);
}
return arr;
}
},
handleLike(detail_id){
let params = {
logintoken:this.logintoken,
loginuid:this.loginuid,
params:{
type:this.$route.query.dynamic_type,
detail_id:detail_id,
source:'h5',
}
};
setLike(params).then(res => {
if(res.data.err_msg == 'success'){
this.zanFlag = true;
this.$vux.toast.text(res.data.info,'middle');
this.initData();

}else{
if(res.data.info != 'NotLogin'){
this.$vux.toast.text(res.data.info,'middle');
}
}
})
},

refrenshCommentNum(data){
Vue.set(this.resData,'plnum',data);
},
countDownProgress(){
if(this.resData.vote_start || this.resData.vote_end){
let current = func.countTime('',this.resData.vote_end);
let totalTime = func.countTime(this.resData.vote_start,this.resData.vote_end);
let progress = (current/totalTime)*100;

this.countDown = current;
this.countProgress = progress.toFixed(2); 
}
},
refrenshGift(){
this.show3 = false;
this.isGiftLayer = false;
},
initVideo(player){
let arr = this.resData.zan;
arr = arr.split('::::::');
this.zanNum = arr.length-1;
},
openPay(){

this.$router.push({path:'/payList',query:{balance:this.balance}});
},
selectItem(id,index,name,price){
this.giftid = id;
this.shade = index;
this.defName = name;
this.defPrice = price;
},
getCommentList(){
this.show1 = !this.show1;
},
setOnclick(){
let params = {'api':'comment/dianzan','id':this.resData.id,'classid':this.resData.classid,'logintoken':this.logintoken,'loginuid':this.loginuid};
setLike(params).then(res => {
if(res.data.err_msg == 'success'){
this.zanFlag = true;


this.$vux.toast.text(res.data.info,'middle');
this.$emit('refrensh');
}else{
if(res.data.info != 'NotLogin'){
this.$vux.toast.text(res.data.info,'middle');
}
}
})
},
popupGift(){
let params = {
logintoken:this.logintoken,
loginuid:this.loginuid,
};
isLogin(params).then(res => {
if(res.data.err_msg == 'success'){
this.show3 = !this.show3;
}else{
let url = window.location.host;
url = staticData.isHttp + '://' + url + '/#/session/fastLogin'; 
let beforeUrl = window.location.href;   
sessionStorage.setItem('beforeUrl',beforeUrl);

func.wxRedirect(url)
}
})
},
closePopup(){
this.show3 = !this.show3;
},
getAttention (userid) {
let params = {
logintoken:this.logintoken,
loginuid:this.loginuid,
params:{
userid:userid
}
};
setAttention(params).then(res => {
if(res.data.err_msg == 'success' && res.data.info == '关注成功！'){
this.isGuanzhu = false;
}
this.$vux.toast.text(res.data.info,'middle');
})
},
openBang(){
this.$router.push({path:'/Ranking',query:{id:this.resData.userid,detail_id:this.resData.id}});
},
openIframe(){
this.$router.push({path:'/introduce',query:{type:2}});
},
alertMsg(){
alert('点击浏览器右上角分享给朋友');
}
}
}



