
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'

import func from '../../../js/handleFunction'
import {setComment, getComment, commentList} from '../../../js/service/getData'
import BScroll from 'better-scroll'

export default {
name: "comment",
data(){
return{
params:{
loginuid:func.getLocalStorage('loginuid'),
logintoken:func.getLocalStorage('logintoken'),
params:{
detail_id:this.$route.query.detail_id,
parent_id:'',
content:'',
source:'h5',
type:this.$route.query.dynamic_type
}
},
pageIndex:1,
pageSize:20,
commentList:[],     //评论列表
isCommentShow:false,    //列表loading
commentTotal:0,    //评论总数
pull_text:false,
pull_end:false,
commentParams:{
loginuid:func.getLocalStorage('loginuid'),
logintoken:func.getLocalStorage('logintoken'),
params:{
detail_id:this.resData.detail_id,
type:this.$route.query.dynamic_type,
page:1,
pageSize:10
}
},
}
},
components:{
Flexbox,FlexboxItem,Group,GroupTitle
},
props:{
resData:{
type:Object,
required:true,
}
},
created(){
this.getCommentList();
},
mounted(){
this.scrollFn()
},
methods:{
scrollFn(){
let _this = this;
this.$nextTick(() => {
if (!this.scroll) {
this.scroll = new BScroll(this.$refs.bscroll, {
click: true,
scrollY: true,
probeType: 3
});
}else{
this.scroll.refresh();
}

this.scroll.on('scroll', (pos) => {

if(this.scroll.maxScrollY>pos.y+5){
this.pull_text = true;
this.pull_end = false;
}else{
this.pull_text = false;
}
})

this.scroll.on('touchEnd', (pos) => {

if(this.scroll.maxScrollY>pos.y+15){
this.loadCommentList();

this.scroll.refresh()
}

})
});
},
sendCommentText(){
let _this = this;
if(this.params.params.content == ''){
return false;
}
setComment(this.params).then(res => {
if(res.data.err_msg == 'success'){
_this.$vux.toast.text(res.data.info,'middle');
_this.params.params.content = '';
_this.getCommentList();
}else{
if(res.data.info != 'NotLogin'){
_this.$vux.toast.text(res.data.info,'middle');
}
}
})
},
getCommentList(){
this.commentList = [];
this.commentParams.params.page = 1;
this.pull_end = false;

commentList(this.commentParams).then(res => {
if(res.data.err_msg == 'success'){
this.commentParams.params.page ++;
this.pull_text = false;
this.commentList = res.data.data.info;
this.commentTotal = res.data.data.account;
this.isCommentShow = true;
this.scroll.refresh();
this.$emit('refrensh',res.data.data.account);
}else{

}
})

},
loadCommentList(){













commentList(this.commentParams).then(res => {
if(res.data.err_msg == 'success'){
for (var i = 0; i < res.data.data.info.length; i++) {
this.commentList.push(res.data.data.info[i]);
}
this.commentParams.params.page ++;
this.pull_text = false;
}else{
this.pull_text = false;
this.pull_end = true;
}
})
},
closeEmit(){
this.$emit('closeChild');
}
}
}



