    import staticData from '../../js/config/url-config.js';
    import { fastLogin, verificationCode } from "../../js/service/getData";
    import func from '../../js/handleFunction'
    import {getUrlParams} from "../../js/func";
	export default {
		name: 'subapp',
		data(){
			return{
                phone:'',	//电话号码
                message_code:'',	//短消息验证码
                isClick:false,	//倒计时
                defText:'获取验证码',
				code:'',	//微信code
				defautBtnText:'快速登录',
			}
		},
		mounted(){

		},
		created(){
            // let sourceUrl = window.location.href;
            var Request = new Object();
            Request = getUrlParams();
            this.code = Request['code'];
		},
		methods:{
            sendCode(){
                let time = 59, _this = this;
                if(this.phone) {
                    this.isClick = true;
                    this.defText = "重新获取(" + time + ")";
                    var clearTime = setInterval(() => {
                        time--;
                        _this.defText = "重新获取(" + time + ")";
                        if(time == 0) {
                            _this.defText = "获取验证码";
                            this.isClick = false;
                            clearInterval(clearTime);
                        }
                    }, 1000);
                } else {
                    alert('请填写手机号');
                }

                let params = {
                    phone:this.phone,
                    Area:'0086',
                    source:'h5',
					name:'',
					user_id:'',
                }

                verificationCode(params).then(res => {
					if(res.data.err_msg != 'success'){
					    alert(res.data.info);
					}
				});
                // this.$ajax.post(staticData.url4 + 'index.php/message-code/fast',params).then(res => {
                //
                // }, res => {
                //
                // });
                // let params = {'api':'user/sendSms','phone':this.phone,'Area':'0086'};
                // getVerificatCode(params).then(res => {
                //
                // })
			},
            fastLogin(){
                let username = '好琴声_'+this.phone;

                if(this.phone == ''){
                    this.$vux.toast.text('请填写正确电话号码', 'middle');
                    return false;
				}
                if(this.message_code == ''){
                    this.$vux.toast.text('验证码不能为空', 'middle');
                    return false;
                }

                this.defautBtnText = '登录中...';

                let params = {
                    phone:this.phone,
                    message_code:this.message_code,
                    openid:this.$route.query.openid,
                    access_token:this.$route.query.access_token,
					// code:this.code,
					source:'wechat',
					// username:username,
                }

                let _this = this;

                fastLogin(params).then(res => {
                    if(res.data.err_msg == 'success'){
                        let tokens = {
                            'logintoken':res.data.data.logintoken,
                            'loginuid':res.data.data.loginuid,
                        }
                        localStorage.setItem('tokens',JSON.stringify(tokens));
                        sessionStorage.setItem('userdata',JSON.stringify(res.data.data));
                        let beforeUrl = sessionStorage.getItem('beforeUrl');
                        localStorage.setItem('openid',res.data.data.openid);

                        window.location.href = beforeUrl;
                    }else{
                        this.defautBtnText = '快速登录';
                        alert(res.data.info);
                        // let url = window.location.host;
                        // url = 'https://'+url+'/#/session/fastLogin'; //跳转到登录注册页
                        // func.wxRedirect(url);
                    }
                });
			}
		},
		components:{

		}
	}



