import { swiper, swiperSlide } from 'vue-awesome-swiper';
import 'swiper/dist/css/swiper.css';
import {adverList} from "../../js/service/getData";

export default {
    name: "advert",
    data(){
        return{
            swiperOption: {
                notNextTick: true,
                autoplay: true,
                direction : 'horizontal',
            },
            // params:{
            //     cmd: 'check',
            //     params: {
            //         type: '86d7437e-58a7-f35d-3e25-abbd897b5314',
            //         owner_id: this.resData.owner_id || this.resData.userInfo.userid,
            //         location: 'cedf7d0f-0311-a4cb-9b4d-ecc7ffa2b426,ab4b6640-fcec-46b1-43f3-56f800a6320d',
            //     }
            // },
            adInfo:[],
        }
    },
    props:{
        resOwner:{
            type:String,
            required:false,
        }
    },
    components:{
        swiper, swiperSlide
    },
    created(){
        this.initData();
    },
    methods:{
        initData(){
            setTimeout(()=>{
                this.getAdver();
            },100);
        },
        async getAdver(){
            let params = {
                cmd: 'check',
                params: {
                    type: '86d7437e-58a7-f35d-3e25-abbd897b5314',
                    owner_id: this.resOwner,
                    location: 'cedf7d0f-0311-a4cb-9b4d-ecc7ffa2b426,ab4b6640-fcec-46b1-43f3-56f800a6320d',
                }
            };
            let res = await adverList(params); //广告
            this.adInfo = res.data.data;
        }
    }
}



