import Vue from 'vue'
import Router from 'vue-router'
import Home from '../views/home/index.vue'
import News from '../views/news/index'
import NewsDetail from '../views/news/newsDetail/index'
import CompetitionList from '../views/activity/index'
import Room from '../views/classroom/index.vue'
import Teacher from '../views/teacher/index'
import TeacherDetail from '../views/teacher/teacherDetail/index'
import Battle from '../views/battle/index'
import CompetitionItem from '../views/activity/activityDetail/index'
import CompetitionContent from '../views/activity/dynamicDetail/index'
import RoomItem from '../views/classroom/roomDetail/index'
import session from '../components/session/index.vue'
import PhoneLogin from '../components/session/phoneLogin'
import fastLogin from '../components/session/fastLogin.vue'
import Center from '../views/center/menu.vue'
import introduce from '../components/iframe/introduce.vue'

import DiaryDetail from '../views/activity/diaryDetail/index'
import AppFrom from '../views/app/form/form'
import ActivityForm from '../views/activity/form/index'

import battleDetail from '../views/battle/battleDetail/index'

Vue.use(Router);

const router = new Router({
    // mode: 'history',
	routes: [
		// {path: '/', component: CompetitionList,name: '',meta:{title:'',requireAuth: true}},
        {path: '/', component: Home,name: 'Home',meta:{title:'音乐日记',requireAuth: true}},
        {path: '/Home', component: Home,name: 'Home',meta:{title:'音乐广场',requireAuth: true}},
        {path: '/News', component: News,name: 'News',meta:{title:'',requireAuth: true}},
        {path: '/CompetitionList', component: CompetitionList,name: 'CompetitionList',meta:{title:'音乐活动',requireAuth: true}},
        {path: '/Room', component: Room,name: 'Room',meta:{title:'琴行教室',requireAuth: true}},
        {path: '/Battle', component: Battle,name: 'Battle',meta:{title:'斗琴擂台',requireAuth: true}},
        {path: '/Teacher', component: Teacher,name: 'Teacher',meta:{title:'名师专区'}},
        {path: '/TeacherDetail', component: TeacherDetail,name: 'TeacherDetail',meta:{title:''}},
        {path: '/CompetitionItem', component: CompetitionItem,name: 'CompetitionItem',meta:{title:'',requireAuth: true}},
        {path: '/CompetitionContent', component: CompetitionContent,name: 'CompetitionContent',meta:{title:'',requireAuth: true}},
        {path: '/Center', component: Center,name: 'Center',meta:{title:'个人中心'}},
        {path: '/RoomItem', component: RoomItem,name: 'RoomItem',meta:{title:'琴行教室'}},
        {path: '/introduce', component: introduce,name: 'introduce',meta:{title:'活动介绍'}},
        {path: '/NewsDetail', component: NewsDetail,name: 'NewsDetail',meta:{title:'音乐讯息',requireAuth: true}},
        {path: '/DiaryDetail', component: DiaryDetail,name: 'DiaryDetail',meta:{title:'',requireAuth: true}},
        {path: '/AppForm', component: AppFrom,name: 'AppFrom',meta:{title:'',requireAuth: true}},
        {path: '/ActivityForm', component: ActivityForm,name: 'ActivityForm',meta:{title:'',requireAuth: true}},
        {path: '/BattleDetail', component: battleDetail,name: 'battleDetail',meta:{title:'',requireAuth: true}},
		{
			path: '/session',
			name: 'session',
			component: session,
			redirect:'/session/fastLogin',
			children: [
                {path: '/session/fastLogin', component: fastLogin,name: 'fastLogin',meta:{title:'好琴声-分享您的音乐生活'}},
                {path: '/session/phoneLogin', component: PhoneLogin,name: 'phoneLogin',meta:{title:'好琴声-分享您的音乐生活'}},
			]
		},
	]
});
router.beforeEach((to,from,next)=>{
	// if(to.matched.some(record => record.meta.requireAuth)){
	// 	if(true){
	// 		next();
	// 	}else{
    //
	// 	}
	// }else {
	// 	next();
	// }

    /* 路由发生变化修改页面title */
    if (to.meta.title) {
        document.title = to.meta.title;
    }
    next();
})


export default router;


