    import {autoLogin} from "../../js/service/getData";
    import func from '../../js/handleFunction'
    import {getUrlParams} from "../../js/func";

	export default {
		name: 'subapp',
		data(){
			return{
                phone:'',	//电话号码
                message_code:'',	//短消息验证码
                isClick:false,	//倒计时
                defText:'获取验证码',
				code:'',	//微信code
			}
		},
		mounted(){

		},
		created(){
            // let sourceUrl = window.location.href;
            var Request = new Object();
            Request = getUrlParams();
            this.code = Request['code'];

            this.login();
		},
		methods:{
            login(){

                let params = {
                    params:{
                        code:this.code,
					}
                }

                autoLogin(params).then(res => {
                    if(res.data.err_msg == 'success'){
                        let tokens = {
                            'logintoken':res.data.data.logintoken,
                            'loginuid':res.data.data.loginuid,
                        }
                        localStorage.setItem('tokens',JSON.stringify(tokens));
                        sessionStorage.setItem('userdata',JSON.stringify(res.data.data));

                        let beforeUrl = sessionStorage.getItem('beforeUrl');
                        localStorage.setItem('openid',res.data.data.openid);
                        
                        // this.$router.go(-2);
                        window.location.href = beforeUrl;
                    }else{
                        this.$router.push({path:'/session/phoneLogin',query:{openid:res.data.data.openid,access_token:res.data.data.access_token}});
                    }
                });
			}
		},
		components:{

		}
	}



